import { RM } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { useKitchenOrderDeck } from '../hooks/useKitchenOrderDeck'
import { useKitchenSectorDeck } from '../hooks/useKitchenSectorDeck'
import { TKitchenCard, TKitchenCardState, TKitchenDeck } from '../kitchen.types'

const sortCards = RM.sortWith<TKitchenCard>([
  RM.descend(RM.prop('isPriority')),
  RM.ascend(RM.prop('time')),
])

const kitchenContext = React.createContext<TKitchenDeck | null>(null)

export const KitchenLoader = observer<SomeChildren>(({ children }) => {
  const { settings } = useRoot()

  const orderDeck = useKitchenOrderDeck(settings.branchId)
  const sectorDeck = useKitchenSectorDeck(settings.branchId)

  const kitchenDeck =
    orderDeck &&
    sectorDeck &&
    RM.mergeDeepWith(RM.concat, orderDeck, sectorDeck)

  const sortedKitchenDeck =
    kitchenDeck && (RM.mapObjIndexed(sortCards, kitchenDeck) as TKitchenDeck)

  return (
    <kitchenContext.Provider value={sortedKitchenDeck}>
      {children}
    </kitchenContext.Provider>
  )
})

export function useKitchenDeck(state: TKitchenCardState) {
  const kitchenDeck = React.useContext(kitchenContext)
  return kitchenDeck && kitchenDeck[state]
}
